<script>
import Header from "../../components/global/header/HeaderArea.vue"
import CitySelecter from "./components/CitySelecter.vue"
import { ref, onMounted, watch } from 'vue'
import { Toast } from '../../components/global/swal.js'
import { apiPharmacy,apiPharmacyAllCity } from "@/api/api"

export default {
  setup() {
    let pharmacyData = ref([])
    let cityAreaData = ref([])
    let city = ref("請選擇縣市")
    let area = ref("請選擇鄉鎮市區")
    let cityIdx = ref(0)
    let areaIdx = ref(0)

    // pharmacyData = pharmacy.data

    const updateCityIdx = (val) => {
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      areaIdx.value = val.value
    }

    watch(() => cityIdx.value, async () => {
      pharmacyData.value = await apiPharmacyAllCity({cityId:cityIdx.value})
      pharmacyData.value = pharmacyData.value.data
    })
    watch(() => areaIdx.value, async () => {
      pharmacyData.value = await apiPharmacy({cityId:cityIdx.value, areaId:areaIdx.value})
      if (pharmacyData.value.data.length == 0) {
        Toast("該區域無資料")
        pharmacyData.value = []
      } else {
        pharmacyData.value = pharmacyData.value.data
      }
    })

    return { pharmacyData, updateCityIdx, updateAreaIdx }
  },
  components: {
    Header, CitySelecter
  },
}
</script>

<template>
  <Header title="藥局" />
  <div class="pharmacy">
    <section class="c-main">
      <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" />
      <div class="resource-container">
        <div class="list-group">
          <div v-for="item in pharmacyData" :key="item.id">
            <a :href=item.website class="list-group-item list-group-item-action">{{ item.name }}</a>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>


<style lang="scss" scoped>
@import "@/assets/custom/config";

.pharmacy {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
</style>